"use client";
import { SocialIcon } from "@/utility/animation";
import Image from "next/image";
import React, { useState } from "react";
import { FaFacebook, FaInstagram, FaLinkedin, FaYoutube } from "react-icons/fa";

const Hero = () => {
  const [activeIcon, setActiveIcon] = useState<string | null>(null);
  const handleClick = (platform: string) => {
    setActiveIcon(platform);
    console.log(`${platform} clicked`);
  };
  return (
    <section className="container mx-auto relative min-h-screen p-2 lg:p-8">
      {/* hero content */}
      <div>
        <div className=" flex gap-2 md:gap-4">
          <SocialIcon
            Icon={FaFacebook}
            isActive={activeIcon === "facebook"}
            onClick={() => handleClick("facebook")}
          />
          <SocialIcon
            Icon={FaYoutube}
            isActive={activeIcon === "youtube"}
            onClick={() => handleClick("youtube")}
          />
          <SocialIcon
            Icon={FaLinkedin}
            isActive={activeIcon === "linkedin"}
            onClick={() => handleClick("facebook")}
          />
          <SocialIcon
            Icon={FaInstagram}
            isActive={activeIcon === "instagram"}
            onClick={() => handleClick("facebook")}
          />
        </div>
        <h1 className="uppercase text-4xl md:text-6xl lg:text-7xl xl:text-8xl font-extrabold mt-36 md:mt-24 lg:mt-36 xl:mt-40 md:leading-tight ">
          BUILD <br />
          SOMETING <br />
          <span className="text-primary">AMAZING</span> <br />
          WITH Hexvon
        </h1>
      </div>
      {/* hero image */}
      <div className=" absolute right-0 top-2 lg:right-16 xl:right-20 md:top-12">
        <Image
          src={"/hero.png"}
          alt="hero"
          width={550}
          height={600}
          className="w-52 md:w-[300px] lg:w-[400px] xl:w-[500px]"
        />
      </div>
    </section>
  );
};

export default Hero;
